import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function netIncomePerEbtTtm(tableName: string) {
  const title = "Net Income per EBT (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["netIncomePerEbtTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Share of pre-tax profit kept as net income.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about net income per EBT or other profitability measures?`,
              value: info.row.original.financialData.companyRatios?.data?.netIncomePerEbtTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.netIncomePerEbtTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { netIncomePerEbtTtm }
